/* Navbar Starts Here */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100vw;
  height: 9rem;
  box-shadow: 0 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 0 10rem;
  background-color: white;
}

.logoDiv .logo {
  width: 18rem;
}

.hamburger {
  display: none;
}

.menu {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.navLink {
  font-size: 1.8rem;
  color: var(--black);
  font-weight: 500;
  cursor: pointer;
  text-transform: capitalize;
}

.activeNavLink,
.navLink:hover {
  color: var(--primary);
  font-weight: 600;
}

.hambugerDiv {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.hamburger button {
  cursor: pointer;
  border: none;
  background-color: transparent;
  outline: none;
}

@media (max-width: 1040px) {
  .navbar {
    padding: 0rem 8rem;
  }
}

@media (max-width: 991px) {
  .menu {
    height: 0px;
    position: absolute;
    top: 8rem;
    flex-direction: column;
    left: 50%;
    transform: translateX(-50%);
    padding: 1rem 0;
    background-color: var(--white);
    gap: 5rem;
    width: 100vw;
    overflow: hidden;
    opacity: 0;
    transition: 0.3s all ease;
  }

  .activeMenu {
    height: 500px;
    opacity: 1;
    justify-content: center;
  }

  .hamburger {
    display: block;
  }
}

@media (max-width: 768px) {
  .navbar {
    padding: 0rem 6rem;
  }
}

@media (max-width: 576px) {
  .navbar {
    padding: 0rem 4rem;
  }
  .downloadCV {
    display: none;
  }
}

@media (max-width: 480px) {
  .navbar {
    padding: 0rem 2rem;
  }
}
